@keyframes rotate{
    from {
      transform: rotate(0deg);
    }
  
    to {
      transform: rotate(360deg);
    }

}

.loader{ 
  padding: 10px;
  border: 6px solid #FF0101;
  border-bottom-color: transparent;
  border-radius: 22px;
  animation: rotate 1s infinite linear;
  height: 0;
  width: 0;
  margin: 20px auto ;
}
